
.node-bworks_careers_block ul{padding:0;list-style:none
}
.node-bworks_careers_block ul a{display:inline-block;width:85%;margin-bottom:1rem;padding-top:.5em;border-top:1px solid #4a4a4a;font-weight:400;color:#4a4a4a
}
.node-bworks_careers_block ul a:after{content:"•";position:absolute;right:calc(15% + 1rem);color:transparent;transition:transform .4s
}
.node-bworks_careers_block ul a.active:after{color:#4b7fe2;transform:scale(2)
}
.node-bworks_careers_block ul a:focus,.node-bworks_careers_block ul a:hover{color:#4b7fe2
}
.node-bworks_careers_block ul a:focus:after,.node-bworks_careers_block ul a:hover:after{color:#4b7fe2;transform:scale(2)
}
.node-bworks_careers_block .job-offer{margin-top:6rem
}
.node-bworks_careers_block .job-offer .apply{margin-top:4rem;text-align:center
}
.node-bworks_careers_block .fade-enter-active,.node-bworks_careers_block .fade-leave-active{transition:opacity .5s
}
.node-bworks_careers_block .fade-enter,.node-bworks_careers_block .fade-leave-to{opacity:0
}



