
#main-menu{position:fixed;top:0;left:0;right:0;bottom:0;width:100%;height:100%;background-color:rgba(75,127,226,0.9);padding-top:10rem;padding-bottom:10vh;z-index:989
}
.menu-wrapper{display:-ms-flexbox;display:flex
}
@media (max-width: 1023px){
.menu-wrapper{-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center;-ms-flex-line-pack:justify;align-content:space-between;height:calc(100vh - 10vh - 10rem)
}
.menu-wrapper .menu-main,.menu-wrapper .menu-contact,.menu-wrapper .menu-language-switcher{width:83.33333%
}
.menu-wrapper .menu-main{-ms-flex-preferred-size:50%;flex-basis:50%
}
.menu-wrapper .menu-contact{-ms-flex-preferred-size:30%;flex-basis:30%
}
.menu-wrapper .menu-social-links{display:none
}
.menu-wrapper .menu-language-switcher{-ms-flex-preferred-size:20%;flex-basis:20%
}
}
@media (min-width: 1024px){
.menu-wrapper{-ms-flex-wrap:wrap;flex-wrap:wrap
}
.menu-wrapper .menu-main,.menu-wrapper .menu-contact{width:50%;-ms-flex-item-align:center;-ms-grid-row-align:center;align-self:center
}
.menu-wrapper .menu-social-links{width:75%;margin-top:4rem
}
.menu-wrapper .menu-language-switcher{width:25%;margin-top:4rem
}
}
.menu-main{font-size:1rem;line-height:2.142857143;letter-spacing:.066428571em
}
.menu-main .line{height:1px;margin-top:.5rem;background-color:#fff;transform:translateX(-100%);transition:transform 0.2s cubic-bezier(1.000, 0.000, 0.750, 1.000)
}
.menu-main li{margin-bottom:.714285714rem;overflow:hidden
}
.menu-main li:hover .line{transform:translateX(0)
}
@media (min-width: 768px){
.menu-main{font-size:2.142857143rem;line-height:1.5;letter-spacing:.066666667em
}
}
.menu-contact .contact h2{color:#fff
}
.menu-contact svg #Shape{fill:#fff
}
.menu-social-links a{font-weight:normal;text-decoration:underline
}
.menu-social-links a+a{margin-left:8.33333%
}
.menu-language-switcher ul{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between
}
@media (max-width: 767px){
.menu-main,.menu-contact,.menu-language-switcher{width:100%
}
}
.menu-enter-active{transition:transform .3s ease-out
}
.menu-enter-active .menu-wrapper{transition:opacity .2s ease-out .1s
}
.menu-enter{transform:translateY(-100%)
}
.menu-enter .menu-wrapper{opacity:0
}
.menu-leave-active{transition:transform 0.3s ease-out
}
.menu-leave-active .menu-wrapper{transition:opacity 0.2s ease-out
}
.menu-leave-to{transform:translateX(100%)
}
.menu-leave-to .menu-wrapper{opacity:0
}



