.animation-wrapper{overflow-x:hidden
}
.animation-wrapper>.animated{transform:translateX(-120%);transition:transform .4s ease-out
}
.run .animation-wrapper>.animated{transform:translateX(0) !important
}
@media (min-width: 768px){
.animation-wrapper>.animated.mirrored{text-align:right;transform:translateX(120%)
}
}
.animation-wrapper>.animated.onhover:focus,.animation-wrapper>.animated.onhover:hover{transform:translateX(0)
}
.body.animation-wrapper>*{opacity:0;transform:translateX(-100%);transition:.4s ease-out;transition-property:opacity, transform;transition-delay:.6s
}
.run .body.animation-wrapper>*{opacity:1;transform:translateX(0)
}
.image.animation-wrapper{background-color:#f3f3f3
}
.image.animation-wrapper img{transform:translateX(-100%);transition:transform .4s ease-out
}
.image.animation-wrapper.run img{transform:translateX(0)
}
a.animated{position:relative;display:inline-block;transform:translateX(calc(100% + 1rem));transition:color .15s ease-out 0s
}
a.animated:after{content:"";position:absolute;width:calc(100% + 1rem);height:1.5em;background-color:#4b7fe2;z-index:-1;transition:transform .2s ease-out;transform-origin:right center;transform:translateX(-200%) scaleY(0.22727)
}
@media (max-width: 767px){
a.animated:after{transform:translateX(-200%) scaleX(0.75) scaleY(0.22727)
}
}
@media (min-width: 1024px){
a.animated:after{transform:translateX(-200%) scaleY(0.22727)
}
}
a.animated:focus,a.animated:hover{color:#fff;transition:color .166s ease-out .034s
}
a.animated:focus:after,a.animated:hover:after{transform:translateX(calc(-100% + .5rem)) scaleX(1) scaleY(1)
}
.node{padding-top:4rem
}
.bg-video{position:absolute;top:0;right:0;bottom:0;left:0;z-index:-100;overflow:hidden
}
.bg-video video{position:absolute;top:50%;left:50%;min-width:100%;min-height:100%;width:auto;height:auto;transform:translateX(-50%) translateY(-50%);background-repeat:no-repeat;background-size:cover
}
#site-header{position:fixed;top:0;width:100%;min-height:75px;padding:calc(2rem + 1vw) 0 1rem;z-index:990
}
#site-header .header-wrapper{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center
}
