}
#site-header .header-wrapper a{color:#4a4a4a
}
#site-header .header-wrapper a.animated:after{background-color:#4a4a4a
}
#site-header .header-wrapper.invert .logo .st0{fill:#fff
}
#site-header .header-wrapper.invert a{color:#fff
}
#site-header .header-wrapper.invert a.animated:after{background-color:#fff
}
#site-header .header-wrapper.invert a.animated:focus,#site-header .header-wrapper.invert a.animated:hover{color:#4a4a4a
}
@media (max-width: 767px){
#site-header .header-wrapper.invert a.animated:after{display:none
}
}
#site-header .logo,#site-header .menu-toggle,#site-header .hire-us{z-index:991
}
#site-header .logo{width:7rem
}
#site-header .logo .st0{transition:.2s
}
#site-header .hire-us{transform:translateX(0)
}
#site-header a{color:#fff;transition:.2s
}
