
.menu-toggle{transform:translateX(-33.333%)
}
.menu-toggle:before{content:"";display:block;width:1px;height:calc(3.78571rem + 1vw);background-color:currentColor;margin:calc(-3.78571rem - .5rem - 1vw) auto 0.5rem;transform:scaleY(0.68);transform-origin:top center;transition:transform .4s
}
.menu-toggle:hover:before{transform:scaleY(1)
}



